import { Vector3 } from '@/AR-Renderer/math/vector3.js';
import { Quaternion } from '@/AR-Renderer/math/quaternion';

class JSON {
	constructor() {}
	toJson() {}
	isValid() {}
}

class FaceTransform extends JSON {

	constructor(position, rotation, scale, camerFOV = 60, faceMeshData = []) {
		super();
		if (!position) {
			position = new Vector3();
		}
		if (!rotation) {
			position = new Quaternion();
		}
		if (!scale) {
			position = new Vector3();
		}

		this.position = position;
		this.rotation = rotation;
		this.scale = scale;
		this.camerFOV = camerFOV;
		this.faceMeshData = faceMeshData;
	}

	toJson() {
		const pos = this.position.toJson();
		const rot = this.rotation.toJson();
		const sc = this.scale.toJson();

		let fm = this.faceMeshData.join();
		return `{
					"position": ${pos}, 
					"rotation": ${rot}, 
					"scale": ${sc}, 
					"camerFOV": ${this.camerFOV}, 
					"facemesh": "${fm}"
				}`;
	}

	isValid() {
		if (this.position && this.rotation && this.scale && this.camerFOV && this.faceMeshData) {
			return true;
		} else {
			return false;
		}
	}
}

class TargetTransform extends JSON {

	constructor(position, rotation, scale, camerFOV = 60) {
		super();
		if (!position) {
			position = new Vector3();
		}
		if (!rotation) {
			position = new Quaternion();
		}
		if (!scale) {
			position = new Vector3();
		}

		this.position = position;
		this.rotation = rotation;
		this.scale = scale;
		this.camerFOV = camerFOV;
	}

	toJson() {
		const pos = this.position.toJson();
		const rot = this.rotation.toJson();
		const sc = this.scale.toJson();

		return `{
					"position": ${pos}, 
					"rotation": ${rot}, 
					"scale": ${sc}, 
					"camerFOV": ${this.camerFOV} 
				}`;
	}

	isValid() {
		if (this.position && this.rotation && this.scale && this.camerFOV) {
			return true;
		} else {
			return false;
		}
	}
}

export { JSON, FaceTransform, TargetTransform };