import { FaceTransform, TargetTransform } from '@/AR-Renderer/classes/messages_classes';



//Face tracking
export function OnFaceFound(controller, unityInstance) {
    unityInstance.SendMessage(controller, 'StartTracking');
}

export function OnFaceTracking(controller, unityInstance, position, rotation, scale, cameraFOV, faceMeshData) {
    SendFaceTransform(controller, unityInstance, position, rotation, scale, cameraFOV, faceMeshData);
}

export function OnFaceLost(controller, unityInstance) {
    unityInstance.SendMessage(controller, 'StopTracking');
}

function SendFaceTransform(controller, unityInstance, position, rotation, scale, cameraFOV, faceMeshData) {
    let faceTransform = new FaceTransform(position, rotation, scale, cameraFOV, faceMeshData);

    if (faceTransform.isValid()) {
        if (unityInstance) {
            unityInstance.SendMessage(controller, "Tracking", faceTransform.toJson());
        }
    } else {
        throw 'Face transform is invalid!';
    }
}
///



//Image Tracking
export function OnTargetFound(controller, unityInstance) {
    unityInstance.SendMessage(controller, 'StartTracking');
}

export function OnTargetTracking(controller, unityInstance, position, rotation, scale, cameraFOV) {
    SendTargetTransform(controller, unityInstance, position, rotation, scale, cameraFOV);
}

export function OnTargetLost(controller, unityInstance) {
    unityInstance.SendMessage(controller, 'StopTracking');
}

function SendTargetTransform(controller, unityInstance, position, rotation, scale, cameraFOV) {
    let targetTransform = new TargetTransform(position, rotation, scale, cameraFOV);

    if (targetTransform.isValid()) {
        if (unityInstance) {
            unityInstance.SendMessage(controller, "Tracking", targetTransform.toJson());
        }
    } else {
        console.error(targetTransform);
        throw 'Target transform is invalid!';
    }
}
