<template>
    <div id="app" >
        <div v-show="true" id="container">
            <canvas id="unity-canvas" 
                    ref="uc">
            </canvas>
            <div id="videoContainer" 
                 ref="vc">
            </div>
        </div>
        
        <div v-if="ShowLoading" 
             id="loaderMain">
            <div id="loadingIndicator">
                <div class="loader"></div>
            </div>
        </div>
    </div>
</template>

<script>

    import * as AR_Renderer from "@/AR-Renderer/ar_renderer";
    import { SceneLoader } from "@/AR-Renderer/scene_loader"

    export default {
        name: 'App',
        data() {
            return {
                sceneLoader: null,
                ShowLoading: false,
                mindARContainerId: "mindArContainer",
            };
        },
        created() {
            addEventListener('beforeunload', () => {
                fetch("/quit").catch((err) => {
                    throw err;
                });
            });
        },
        mounted() {
            this.Initialization();
        },
        methods: {
            async Initialization() {
                let unityContainer = this.$refs.uc;
                let videoContainer = this.$refs.vc;

                this.sceneLoader = new SceneLoader(unityContainer, videoContainer, this);

                AR_Renderer.Init(unityContainer, () => {});
            },

            ShowLoadingIndicator(state) {
                this.ShowLoading = state;
            },
        }
    }
</script>

<style>
    @import '../css/styles.css';

    #unity-canvas {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1;
    }

    #container {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 0;
    }

    #videoContainer {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 0;
    }

    #mindArContainer {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 0;
    }

    #loaderMain {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        background-color: #3b3b3b
    }

    #loadingIndicator {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .loader {
        border: 16px solid #f3f3f3; /* Light grey */
        border-top: 16px solid #3498db; /* Blue */
        border-radius: 50%;
        width: 120px;
        height: 120px;
        animation: spin 2s linear infinite;
    }

    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }
</style>
