import { Matrix4x4 } from "@/AR-Renderer/math/matrix4x4";
import { Vector3 } from "@/AR-Renderer/math/vector3";
import { Quaternion } from "@/AR-Renderer/math/quaternion";
import {
    OnTargetFound,
    OnTargetTracking,
    OnTargetLost
    ,
    OnFaceFound,
    OnFaceTracking,
    OnFaceLost
    ,
    OnSceneLoaded,
} from "./bridge";

let UnityInstance;


export function Init(canvas, callback) {
    var buildUrl = `./Build`;
    var loaderUrl = buildUrl + "/{{{LOADER_FILENAME}}}";
    var config = {
        dataUrl: buildUrl + "/{{{DATA_FILENAME}}}",
        frameworkUrl: buildUrl + "/{{{FRAMEWORK_FILENAME}}}",
        codeUrl: buildUrl + "/{{{CODE_FILENAME}}}",
        streamingAssetsUrl: "StreamingAssets",
        companyName: "{{{ COMPANY_NAME }}}",
        productName: "{{{ PRODUCT_NAME }}}",
        productVersion: "{{{ PRODUCT_VERSION }}}",
    };

    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;

    var script = document.createElement("script");
    script.src = loaderUrl;
    script.onload = () => {
        // eslint-disable-next-line no-undef
        createUnityInstance(canvas, config)
            .then((unityInstance) => {
                UnityInstance = unityInstance;
                callback(unityInstance);
            })
            .catch((message) => {
                //Handle error
            });
    };
    canvas.appendChild(script);
}


//Image and Surface
export function onTargetFound(controller) {
    OnTargetFound(controller, UnityInstance);
}

export function onTargetTracking(controller, sceneMatrixElements, cameraFOV) {
    DecomposeSceneMatrix(sceneMatrixElements, (position, rotation, scale) => {
        OnTargetTracking(controller, UnityInstance, position, rotation, scale, cameraFOV);
    });
}

export function onTargetLost(controller) {
    OnTargetLost(controller, UnityInstance);
}
///



//Face
export function onFaceFound(controller) {
    OnFaceFound(controller, UnityInstance);
}

export function onFaceTracking(controller, sceneMatrixElements, cameraFOV, faceMeshData) {
    DecomposeSceneMatrix(sceneMatrixElements, (position, rotation, scale) => {
        OnFaceTracking(controller, UnityInstance, position, rotation, scale, cameraFOV, faceMeshData);
    });
}

export function onFaceLost(controller) {
    OnFaceLost(controller, UnityInstance);
}
///


//Uni
export function onSceneLoaded(controller, sceneIndex) {
    OnSceneLoaded(controller, UnityInstance, sceneIndex);
}



function DecomposeSceneMatrix(sceneMatrixElements, callback) {
    const matrix = new Matrix4x4(sceneMatrixElements);

    const position = new Vector3();
    const scale = new Vector3();
    const rotation = new Quaternion();

    matrix.decompose(position, rotation, scale);
    return callback(position, rotation, scale);
}